import React, { Component } from 'react'
import "../main.css"

import { NavLink } from "react-router-dom";
import Cookie from "js-cookie";
import Pubsub from 'pubsub-js';

export class Header extends Component {
  constructor(props) {
    super(props)
    this.state = {
      auth: "",
    }
  }
  componentDidMount() {
    var that = this;
    Pubsub.subscribe("auth", function (msg, data) {
      // console.log(msg, data);
      that.setState({
        auth: data
      })
    })
  }
  render() {
    if (Cookie.get("auth")) {
      return (
        <nav className="navbar navbar-light">
          <div className="container">
            <a className="navbar-brand" href="index.html">conduit</a>
            <ul className="nav navbar-nav pull-xs-right">
              <li className="nav-item">
                <NavLink className="nav-link" to="/">Home</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/editor" activeClassName="active">
                  <i className="ion-compose"></i>&nbsp;New Article
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/settings" activeClassName="active">
                  <i className="ion-gear-a"></i>&nbsp;Settings
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link ng-binding active" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })" to="/profile" activeClassName="active">
                  liu9527
                </NavLink>
              </li>
            </ul>
          </div>
        </nav>
      )
    } else {
      return (
        <nav className="navbar navbar-light">
          <div className="container">
            <a className="navbar-brand" href="index.html">conduit</a>
            <ul className="nav navbar-nav pull-xs-right">
              <li className="nav-item">
                <NavLink className="nav-link" to="/">Home</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/login" activeClassName="active">Sign in</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/register" activeClassName="active">Sign up</NavLink>
              </li>
            </ul>
          </div>
        </nav>
      )
    }
  }
}

export default Header
